import Grid from "@mui/material/Grid";
import Stack from "@mui/material/Stack";
import React from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";
import doctorImg from "../../assets/doctors.png";
import { PRI_400 } from "../../theme/colors";
import makeStyles from "@mui/styles/makeStyles";
import CalendarTodayOutlined from "@mui/icons-material/CalendarTodayOutlined";
import ModalNotification from "../ModalNotification";
import handSucc from "../../assets/hand_succ.png";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { batalAppointment } from "../../redux/actions";
import Dialog from "@mui/material/Dialog";
import IconButton from "@mui/material/IconButton";
import TextField from "@mui/material/TextField";
import Close from "@mui/icons-material/Close";
import Receipt from "@mui/icons-material/Receipt";
import failedImg from "../../assets/hand_fail.png";
import QRCode from "react-qr-code";

const useStyles = makeStyles({
  img: {
    width: "50px",
    height: "50px",
    objectFit: "contain",
    borderRadius: "50%",
  },
  dateContainer: {
    borderRadius: "8px !important",
    color: "#FFF !important",
    backgroundColor: PRI_400,
    padding: "8px",
  },
  cancelStamp: {
    position: "absolute",
    padding: "8px",
    border: "3px solid red",
    backgroundColor: "#FFF",
    opacity: "0.8",
    color: "red",
    fontSize: "16px",
    width: "50vw",
    fontWeight: 700,
    textAlign: "center",
  },
  qrWrapper: {
    position: "relative",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
});
const HistoryCard = (props) => {
  const {
    stateBahasa,
    title,
    subtitle,
    time,
    tanggal,
    cancelled,
    rkunId,
    actBatalAppointment,
  } = props;

  const classes = useStyles();
  const [notif, setNotif] = React.useState(false);
  const [done, setDone] = React.useState(false);
  const [alasan, setAlasan] = React.useState("");
  const [openFail, setOpenFail] = React.useState({
    open: false,
    message: "",
  });
  const [receipt, setReceipt] = React.useState({
    open: false,
  });
  const handleFail = (msg) => {
    setOpenFail({
      open: true,
      message: msg,
    });
  };
  const handleCancel = () => {
    actBatalAppointment(
      {
        rkun_id: rkunId,
        alasan: alasan,
      },
      {
        success: () => setDone(true),
        failed: (msg) => handleFail(msg),
      }
    );
  };
  const handleReceipt = () => {
    setReceipt({
      open: true,
    });
  };

  return (
    <>
      <Dialog open={receipt.open}>
        <Paper
          sx={{
            padding: "16px",
          }}
        >
          <Grid container spacing={2}>
            <Grid item xs={12} container justifyContent="end">
              <IconButton
                onClick={() =>
                  setReceipt({
                    open: false,
                  })
                }
              >
                <Close />
              </IconButton>
            </Grid>
            <Grid item xs={12} container direction="column">
              <Typography variant="h3" align="center">
                RSUD CENGKARENG
              </Typography>
              <Typography variant="body2" align="center">
                JL. Bumi Cengkareng Indah No.1, Cengkareng Tim., Kecamatan
                Cengkareng, Kota Jakarta Barat, Daerah Khusus Ibukota Jakarta
                11730
              </Typography>
              <Typography
                variant="body2"
                align="center"
                color="blue"
                onClick={() =>
                  window.open("https://goo.gl/maps/n1MF6RVaRYdUfR5q6", "_blank")
                }
              >
                Google Maps
              </Typography>
            </Grid>
            <Grid item xs={12} container direction="column">
              <Typography variant="subtitle1" align="center">
                BPJS - LANSIA
              </Typography>
            </Grid>
            <Grid item xs={12} container justifyContent="center">
              <div className={classes.qrWrapper}>
                {cancelled && (
                  <Box className={classes.cancelStamp}>CANCELED</Box>
                )}
                <QRCode value="7" size={128} />
              </div>
            </Grid>
            <Grid item xs={12}>
              <Typography variant="body1">
                {stateBahasa === "indonesia"
                  ? "Nomor antrian anda:"
                  : "Your queue:"}
              </Typography>
              <Box
                sx={{
                  padding: "3px",
                  borderRadius: "3px",
                  border: "2px solid " + PRI_400,
                }}
              >
                <Typography variant="h3" align="center">
                  10
                </Typography>
              </Box>
            </Grid>
            <Grid item xs={12}>
              <Typography variant="body1">
                {stateBahasa === "indonesia"
                  ? "Estimasi waktu tindakan:"
                  : "Estimated time:"}
              </Typography>
              <Box
                sx={{
                  padding: "3px",
                  borderRadius: "3px",
                  border: "2px solid " + PRI_400,
                }}
              >
                <Typography variant="subtitle1" align="center">
                  16 DESEMBER 2020 / 08:00 - 09:00
                </Typography>
              </Box>
            </Grid>
            <Grid item xs={12}>
              <Typography variant="body1" align="center">
                {stateBahasa === "indonesia"
                  ? "SILAHKAN MENUNGGU NOMOR ANTRIAN DIPANGGIL, MOHON DATANG SETENGAH JAM SEBELUM ESTIMASI"
                  : "PLEASE WAIT FOR YOUR TURN, AND COME HALF HOUR EARLIER"}
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Typography variant="subtitle2" align="center">
                {stateBahasa === "indonesia"
                  ? "TERIMAKASIH ATAS KEPERCAYAAN ANDA"
                  : "THANK YOU FOR YOUR TRUST"}
              </Typography>
            </Grid>
          </Grid>
        </Paper>
      </Dialog>
      <Dialog open={notif}>
        <Paper sx={{ padding: "8px" }}>
          <Grid container spacing={2}>
            <Grid item xs={12} container justifyContent="end">
              <IconButton onClick={() => setNotif(false)}>
                <Close />
              </IconButton>
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                label={stateBahasa === "indonesia" ? "Alasan Batal" : "Reason"}
                value={alasan}
                onChange={(e) => setAlasan(e.target.value)}
              />
            </Grid>
            <Grid item xs={12}>
              <Button
                variant="contained"
                disabled={!alasan}
                onClick={handleCancel}
              >
                Submit
              </Button>
            </Grid>
          </Grid>
        </Paper>
      </Dialog>
      <ModalNotification
        open={done}
        setOpen={setDone}
        title={stateBahasa === "indonesia" ? "Dibatalkan" : "Cancelled"}
        subtitle={""}
        img={handSucc}
      />
      <ModalNotification
        open={openFail.open}
        setOpen={(open) => setOpenFail({ ...openFail, open: open })}
        title={"Failed"}
        subtitle={openFail.message}
        img={failedImg}
      />

      <Paper
        sx={{
          padding: "15px 10px",
        }}
      >
        <Grid container spacing={1}>
          <Grid
            item
            xs={12}
            container
            spacing={2}
            justifyContent="space-evenly"
          >
            <Grid item>
              <img src={doctorImg} alt="" className={classes.img} />
            </Grid>
            <Grid item>
              <div>
                <Stack>
                  <Typography variant="subtitle2">{title}</Typography>
                  <Typography variant="body1">{subtitle}</Typography>
                </Stack>
              </div>
            </Grid>
            <Grid item>
              <IconButton onClick={handleReceipt}>
                <Receipt />
              </IconButton>
            </Grid>
          </Grid>
          <Grid item xs={12}>
            <Box className={classes.dateContainer}>
              <Grid container spacing={2}>
                <Grid
                  item
                  xs
                  container
                  justifyContent="center"
                  alignItems="center"
                >
                  <CalendarTodayOutlined />
                  {tanggal}
                </Grid>
                {/* <Grid
                  item
                  xs
                  container
                  justifyContent="center"
                  alignItems="center"
                >
                  <Timer />
                  08:00 - 16:00
                </Grid> */}
              </Grid>
            </Box>
          </Grid>
          <Grid item container xs={12} spacing={2}>
            {/* <Grid item xs>
              <Button variant="contained">Reschedule</Button>
            </Grid> */}
            <Grid item xs>
              {!cancelled && (
                <Button
                  variant="outlined"
                  onClick={() => setNotif(true)}
                  disabled={cancelled}
                >
                  Cancel
                </Button>
              )}
              {cancelled && (
                <Typography
                  variant="body1"
                  color="text.secondary"
                  align="center"
                >
                  {stateBahasa === "indonesia" ? "Dibatalkan" : "Cancelled"}
                </Typography>
              )}
            </Grid>
          </Grid>
        </Grid>
      </Paper>
    </>
  );
};
HistoryCard.propTypes = {
  time: PropTypes.string,
  tanggal: PropTypes.string,
  title: PropTypes.string.isRequired,
  subtitle: PropTypes.string.isRequired,
  // cancelled: PropTypes.bool.isRequired,
  rkunId: PropTypes.any.isRequired,
};

const mapStateToProps = (state) => {
  return {
    // stateEmail: state.auth.email,
    stateBahasa: state.app.bahasa,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actBatalAppointment: (body, action) =>
      dispatch(batalAppointment(body, action)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HistoryCard);
