import Grid from "@mui/material/Grid";
import Stack from "@mui/material/Stack";
import Container from "@mui/material/Container";
import React from "react";
import makeStyles from "@mui/styles/makeStyles";
import BottomNav from "../../components/BottomNav";
import TopNav from "../../components/TopNav";
import { useNavigate } from "react-router-dom";
import { connect } from "react-redux";
import {
  batalAppointment,
  getHistoryAppointment,
  getPaketMCU,
} from "../../redux/actions";
import HistoryCard from "../../components/HistoryCard";
const useStyles = makeStyles(() => ({
  textContent: {
    padding: "0 8px !important",
    flexGrow: 100,
  },
  filterContent: {
    flexGrow: 100,
  },
  searchBar: {
    display: "flex",
  },
  root: {
    paddingTop: "32px",
    paddingBottom: "128px",
  },
  chip: {},
}));

const HistoryPage = (props) => {
  const {
    actGetPaketMCU,
    stateBahasa,
    actGetHistoryAppointment,
    stateHistory,
    actBatalAppointment,
  } = props;
  const classes = useStyles();
  const navigate = useNavigate();
  const [batalDialog, setBatalDialog] = React.useState({
    open: false,
    rkun_id: "",
  });
  const handleCancel = (rkun_id) => {
    return actBatalAppointment({
      rkun_id: "",
      alasan: "",
    });
  };
  React.useEffect(() => {
    // actGetPaketMCU({ mcu_id: null, mcu_name: null });
    actGetHistoryAppointment(null, () => navigate('/login'));
  }, []);

  const [search, setSearch] = React.useState("");

  const handleSubmitSearch = () => {
    actGetPaketMCU({ mcu_id: null, mcu_name: search });
  };

  const handleBack = () => {
    navigate(-1);
  };
  const closeDialog = () => {
    setBatalDialog({
      open: !batalDialog.open,
      ...batalDialog,
    });
  };
  return (
    <>
      <TopNav
        title={stateBahasa === "indonesia" ? "Riwayat" : "History"}
        subtitle={
          stateBahasa === "indonesia"
            ? "Temukan riwayat konsultasi anda"
            : "Find your consultation history"
        }
        variant="with-background"
        click={handleBack}
      />
      <Container className={classes.root}>
        <Stack spacing={2}>
          {/* <Swiper
            pagination={true}
            slidesPerView={4}
            spaceBetween={10}
            className="mySwiper"
          >
            {kamarList.map((item, index) => {
              return (
                <SwiperSlide
                  key={"carousel_" + index}
                  onClick={() => handleChangeRoom(item)}
                >
                  <CustomChip
                    variant={filter.ruang === item ? "selected" : "default"}
                    label={item}
                  />
                </SwiperSlide>
              );
            })}
          </Swiper> */}
          <div>
            <Grid container spacing={2}>
              {stateHistory &&
                stateHistory.map((item) => {
                  return (
                    <Grid
                      item
                      xs={12}
                      md={4}
                      key={item.rkun_id + "_hist"}
                    >
                      <HistoryCard
                        title={item.dokter.peg_nama}
                        subtitle={item.layanan.ref_layanan_nama}
                        tanggal={item.rkun_tgl_visit}
                        cancelled={item.rkun_batal}
                        rkunId={item.rkun_id}
                        // oncancel={() =>
                        //   batalDialog({
                        //     open: true,
                        //     rkun_id: item.rkun_id,
                        //   })
                        // }
                      />
                    </Grid>
                  );
                })}
            </Grid>
          </div>
        </Stack>
        <BottomNav />
      </Container>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    statePaketMCU: state.master.paketMCU,
    stateBahasa: state.app.bahasa,
    stateHistory: state.register.historyAppointment,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actGetPaketMCU: (body) => dispatch(getPaketMCU(body)),
    actGetHistoryAppointment: (body, action) =>
      dispatch(getHistoryAppointment(body, action)),
    actBatalAppointment: (body, action) =>
      dispatch(batalAppointment(body, action)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HistoryPage);
